.mobile_menu {
  width: 70%;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  padding: 80px 30px 0 30px;
  background-color: white;
  max-width: 450px;
}

.mobile_menu ul {
  /* padding-bottom: 1rem; */
}

.mobile_menu ul li {
  width: 150px;
  display: flex;
  cursor: pointer;
  position: relative;
  flex-direction: column;
  padding: 0 0 1.2rem;
}

.mobile_menu ul li a,
.mobile_menu .auth .login,
.mobile_menu .auth .register {
  color: hsl(0, 0%, 45%);
  font-size: 0.9rem;
  font-weight: 500;
}

.mobile_menu .auth {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

/* Login and Register styling */
.mobile_menu .auth .login,
.mobile_menu .auth .register {
  padding: 0.8rem 0;
  border-radius: 1rem;
  cursor: pointer;
  background-color: white;
}

.mobile_menu .auth .login {
  border: none;
}

.mobile_menu .auth .register {
  border: 2px solid hsl(0, 0%, 55%);
  outline: none;
}

/* Dropdown Styling */
.mobile_menu ul li .dropdown {
  padding: 1rem 0 0 1rem;
}

.mobile_menu ul li .dropdown ul li {
  padding: 0.5rem 0 0.5rem 0.8rem;
}

@media screen and (min-width: 750px) {
  .mobile_menu {
    width: 100%;
    position: relative;
    /* background-color: green; */
    padding: 0;
    min-width: 500px;
  }
  .mobile_menu ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
  }
  .mobile_menu ul li {
    width: 200px;
    position: relative;
    flex-direction: row;
    /* background-color: blueviolet; */
    padding: 0;
  }

  .mobile_menu ul li a {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .mobile_menu .auth {
    flex-direction: row;
    gap: 0.3rem;
  }
  .mobile_menu .auth .login,
  .mobile_menu .auth .register {
    padding: 0.5rem 1rem;
  }
}
